@import url(https://fonts.googleapis.com/css?family=Amatic+SC);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
html body {
    height: 100vh;
    width: 100vw;
    background-color: #f4f4f8;
    font-family: "Josefin Sans";
}

.something__container {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh;
}

.pennies__header {
  line-height: 3;
  margin-bottom: 15rem;
  text-align: center;

  color: #fe4a49;
  font-family: "Amatic SC";
  font-size: 90px;
  font-weight: 1000;
}

.thoughts__container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  width: 100%;
}

.thoughts__current {
  display: inline;
  width: 50%;


  color: #fed766;
  line-height: 2em;
  font-size: 36px;
  text-align: center;
}

.thoughts__current span {
  padding: 0.2em;
  box-shadow: 0.2em 0 0 rgba(cadetblue, 0.7), -0.2em 0 0 rgba(cadetblue, 0.7);
  background-color: #2ab7ca;
  opacity: 0.7;
}

.thoughts__list {
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.16);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-height: 400px;
  line-height: 2;
  overflow: auto;
  width: 250px;

  color: #2ab7ca;
  font-weight: 300;

  background-color: #e6e6ea;
}

.thoughts__list--selected {
  text-align: center;
  color: #fed766;
  background-color: #2ab7ca;
  opacity: 0.7;
  width: 100%;
}
